const ProductListing = async ({ searchParams }) => {
  const wixClient = await wixClientServer();

  // ✅ Extract the category slug from searchParams
  const categorySlug = searchParams?.category || "all-products";
  console.log("Fetching category for slug:", categorySlug); // ✅ Debugging log

  try {
      // ✅ Log if the function is being called
      console.log("Calling wixClient.collections.getCollectionBySlug...");

      const cat = await wixClient.collections.getCollectionBySlug(categorySlug);
      
      console.log("Fetched category data:", cat); // ✅ Debugging log

      return (
          <section className="product_page">
              <div className="container">
                  <div className="inner_">
                      <Filter />
                      <h3 className="title">{cat?.collection?.name} For You!</h3>
                      <ProductPage 
                          key={JSON.stringify(searchParams)} 
                          categoryId={cat?.collection?._id || "00000000-000000-000000-000000000001"} 
                          searchParams={searchParams} 
                      />
                  </div>
              </div>
          </section>
      );
  } catch (error) {
      console.error("Error fetching category:", error);
      return <div>Error loading category: {error.message}</div>;
  }
};

export default ProductListing;
